
.homeBody {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;

  .decodeButton{
    width: 100px;
    height: 50px;

  }

  .navigationH1:hover{
    margin-top: 0;
    cursor: pointer;
    color: #b58900;
  }

  .textBlock {
    display: flex;
    font-size: 60px;
    color: #859900;

  }

  .fadeOut{
    display: none;
    transition: width 2s 2s, height 2s 2s, opacity 2s;
    font-size: 60px;
    color: #859900;
    opacity:0;
  }
  .mainElements{
    display: flex;
    transition: width 2s 2s, height 2s 2s, opacity 2s;
    color: #859900;
    opacity: 100;
    flex-direction: column;
    width: 90vw;
    height: 90vh;

    .mainRow{
      display: flex;
      flex-direction: row;
      flex: 1;
      padding: 20px;

      .mainQuadrant{
        display: flex;
        flex-direction: column;
        flex: 1;
        padding: 10px;

        .titleButton{
          display: flex;
          width: fit-content;
          height: 40px;
          padding: 7px 22px 7px;
          margin: 0 0 20px;
          align-items: center;
          justify-content: center;


          .navigationH1{
            margin: 0;
            color: #859900;
          }
        }
        .titleButton:hover{
          padding: 5px 20px 5px;
          border-radius: 50px;
          border: 2px solid #b58900;
          .navigationH1:hover{
            color: #b58900;
          }
        }
        .paragraphBlock{
          display: flex;
          flex: 1;
          flex-direction: column;
          margin: 10px 50px 20px;
          color: #859900;
          font-size: 20px;

        }
        .paragraph{
          margin-top: 0.5em;
          margin-bottom: 0.5em;
        }
      }


    }
  }
  .mainElementsHidden{
    display: none;
    opacity: 0;
  }

  .buttonDiv{
    padding-bottom: 20px;
  }

  .statButton{
    font-family: "Ubuntu Light", source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    background: #eee8d5;
    font-size: 16px;
    color: #073642;
    width: 100px;
    height: 50px;
    border-radius: 20px;
  }
  .statButton:hover{
    font-family: "Ubuntu Light", source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
    background: #eee8d5;
    font-size: 16px;
    color: #b58900;
    width: 100px;
    height: 50px;
    border-radius: 20px;
  }

  .youtubeEmbed{
    height: 100%;
  }

  @media only screen and (max-width: 600px) {
    .mainRow{
      display: flex;
      flex-direction: column;
      flex: 1;
      padding: 20px;
    }
  }
}

/*
  Animation for transitions.
 */
#cursor {
  background: #073642;
  box-sizing: border-box;
  border-left: .5em solid;
  -webkit-animation: typing 3s steps(19, end),blinking 1s step-end infinite;
  animation: typing 3s steps(19, end),blinking 1s step-end infinite;

}

#slide {
  position: absolute;
  left: -100px;
  width: 100px;
  height: 100px;
  background: blue;
  -webkit-animation: slide 0.5s forwards;
  -webkit-animation-delay: 2s;
  animation: slide 0.5s forwards;
  animation-delay: 2s;
}

@keyframes blinking {
  0% {border-color: transparent;}
  100% {border-color: transparent;}
  50% {border-color: #859900;}
}
